.Fixedfooter{
    width: 1443px;
    height: 65px;
    margin-top: 38.5vmax;
    display: flex;
    justify-content: space-between;
    align-items: center;

}
.policytext{
    position: relative;
    width: 363px;
    height: 65px;
    font-size: 10px;
    line-height: 13px;
    color: white;
    text-align: start;
    margin-left: 12vmax;
    z-index: 1;
   


   
}
.downloadicons img{
    position: relative;
    margin: 1vmax;
    width: 170px;
    height: 50px;
    z-index: 1;
    cursor: pointer;

}
.Buttons img{
    width: 21px;
    height: 21px;
    margin-left: 1vmax;
    padding: 0.5vmax;
    cursor: pointer;
}
.vector{
    position: relative;
    width: 18px;
    height: 30px;
    margin-left: 19vmax;
    cursor: pointer;
}
@media screen and (max-width: 768px) {
    .Fixedfooter{
        width: 350px;
        height: 1%;
        margin-top: 38.5vmax;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        margin-right: 58vmax;
        margin-top: -33vmax;
    
    }
    .policytext{
        position: relative;
        width: 300px;
        height: 65px;
        color: white;
        text-align: start;
        margin-left: 12vmax;
        z-index: 1;
        font-size: 11px;
        margin: auto;
       
    
    
       
    }
    .Buttons img{
        width: 21px;
        height: 21px;
        margin-left: 1vmax;
        padding: 0.5vmax;
        cursor: pointer;
        margin-top: 8vmax;
     
    }
    .vector{
        position: relative;
        width: 18px;
        height: 30px;
        margin-left: 19vmax;
        cursor: pointer;
        margin: auto;
        padding: 1vmax;
    }

}